<template>
  <div class="wrap">
    <div class="left">
      <DayPilotNavigator id="nav" :config="navigatorConfig" ref="navigator" />
    </div>
    <div class="content">
      <DayPilotCalendar id="dp" :config="calendarConfig" ref="calendar" />
    </div>
  </div>
</template>

<script>
import { computed, reactive, ref, onMounted } from 'vue';
import { DayPilot, DayPilotCalendar, DayPilotNavigator } from '@daypilot/daypilot-lite-vue';
import axios from 'axios';

export default {
  name: 'ResourceCalendar',
  components: {
    DayPilotCalendar,
    DayPilotNavigator
  },
  setup() {
    const calendar = ref(null);
    const navigator = ref(null);

    const state = reactive({
      selectedDay: "2023-08-01", // Initialize with the default start date
    });

    const navigatorConfig = {
      showMonths: 3,
      skipMonths: 3,
      selectMode: "Day",
      startDate: state.selectedDay,
      selectionDay: state.selectedDay,
      onTimeRangeSelected: args => {
        state.selectedDay = args.day;
        loadEvents(state.selectedDay);
        calendarConfig.value.startDate = args.day; // Update startDate in calendarConfig
        calendarConfig.value.selectionDay = args.day;
      }
    };

    const calendarConfig = computed(() => ({
      viewType: "Resources",
      startDate: state.selectedDay,
      timeRangeSelectedHandling: "Enabled",
      onTimeRangeSelected: async (args) => {
        await createEvent(args.start, args.end, args.resource);
        calendar.value.control.clearSelection();
      },
      onEventClick: (args) => {
        const contextMenu = new DayPilot.Menu({
          items: [
            { text: "Edit", onClick: () => editEvent(args.e) },
            { text: "Delete", onClick: () => deleteEvent(args.e) },
          ]
        });
        contextMenu.show(args.originalEvent);
      },
      eventDeleteHandling: "Enabled",
      onEventMoved: (args) => {
        editEvent(args.e);
        console.log("Event moved", args.e);
      },
      onEventResized: (args) => {
        console.log("Event resized", args.e);
      },
    }));

    const deleteEvent = (e) => {
      axios.post("https://calendarproj.azurewebsites.net/delete", e.data)
        .then(response => {
          if (response.status === 200) {
            console.log("Event deleted", e);
            calendar.value.control.events.remove(e);
          } else {
            console.log("Event delete failed", e);
          }
        })
        .catch(error => {
          console.log(error);
        });
    };

    const loadEvents = (day) => {
      axios.get('https://calendarproj.azurewebsites.net/default', { params: { day: day } })
        .then(response => {
          const data = response.data;
          const events = data.map(item => {
            return {
              id: item.id,
              start: item.start,
              end: item.end,
              resource: item.resource,
              text: item.text,
              barColor: "#38761d",
            };
          });
          calendar.value.control.update({ events });
          navigator.value.control.update({ events });
        })
        .catch(error => {
          console.log(error);
        });
    };

    const loadResources = () => {
      const columns = [
        { name: "Room 1", id: "R1" },
        { name: "Room 2", id: "R2" },
        { name: "Room 3", id: "R3" },
      ];
      calendar.value.control.update({ columns });
    };

    const editEvent = async (e) => {
      const form = [
        { name: "Text", id: "text" },
        { name: "Start", id: "start", type: "datetime", disabled: true },
        { name: "End", id: "end", type: "datetime", disabled: true },
        { name: "Room", id: "resource", type: "select", options: calendar.value.control.columns.list }
      ];

      const data = e.data;

      const modal = await DayPilot.Modal.form(form, data);
      if (modal.canceled) {
        return;
      }

      axios.post("https://calendarproj.azurewebsites.net/update", modal.result)
        .then(response => {
          if (response.status === 200) {
            console.log("Event updated", modal.result);
            
            calendar.value.control.events.update(modal.result);
            window.alert(response.data);
          } else if (response.status === 403) {
            console.log("Event was not edited due to profanity", e);
            window.alert("Event was not edited due to profanity");
          } else {
            console.log("Event update failed", data);
          }
        })
        .catch(error => {
          console.log(error);
          window.alert("Event was not edited due to profanity");
        });
    };

    const createEvent = async (start, end, resource) => {
      const form = [
        { name: "Text", id: "text" },
        { name: "Start", id: "start", type: "datetime", disabled: true },
        { name: "End", id: "end", type: "datetime", disabled: true },
        { name: "Room", id: "resource", type: "select", options: calendar.value.control.columns.list }
      ];

      const data = {
        start,
        end,
        resource,
        id: DayPilot.guid()
      };

      const modal = await DayPilot.Modal.form(form, data);
      if (modal.canceled) {
        return;
      }

      const e = modal.result;
      axios.post('https://calendarproj.azurewebsites.net/insert', e)
        .then(response => {
          if (response.status === 200) {
            console.log("Event created", response.data);
            calendar.value.control.events.add(e);
            window.alert(response.data);
          } else if (response.status === 403) {
            console.log("Event was not created due to profanity", e);
            window.alert("Event was not created due to profanity");
          }else {
            console.log("Event creation failed", e);
          }
        })
        .catch(error => {
          window.alert("Event was not created due to profanity");
          console.log(error);
        });
    };

    onMounted(() => {
      loadResources();
      loadEvents();
    });

    return {
      navigatorConfig,
      calendarConfig,
      calendar,
      navigator,
      loadEvents,
      loadResources,
      editEvent,
      createEvent
    };
  }
};
</script>

<style>
.wrap {
  display: flex;
}

.left {
  margin-right: 10px;
}

.content {
  flex-grow: 1;
}

.navigator_default_busy.navigator_default_cell {
  border-bottom: 4px solid #ee4f2ecc;
  box-sizing: border-box;
}
</style>